import uniIcons from '../uni-icons/uni-icons.vue'
export default {
	data() {
		return {
			statusBarHeight: '50px;'
		};
	},
	components: {
		uniIcons
	},
	props: {
		url:{
			type: String,
			default: '/pages/regist/regist'
		},
		title: {
			type: String,
			default: ''
		},
		leftText: {
			type: String,
			default: ''
		},
		rightText: {
			type: String,
			default: ''
		},
		leftIcon: {
			type: String,
			default: ''
		},
		rightIcon: {
			type: String,
			default: ''
		},
		fixed: {
			type: [Boolean, String],
			default: true
		},
		color: {
			type: String,
			default: '#000000'
		},
		backgroundColor: {
			type: String,
			default: '#FFFFFF'
		},
		shadow: {
			type: [String, Boolean],
			default: true
		},
		border: {
			type: [String, Boolean],
			default: false
		}
	},
	mounted() {
		var statusBarHeight = uni.getSystemInfoSync().statusBarHeight + 50;
		this.statusBarHeight = statusBarHeight + 'px';
	},
	methods: {
		onClickLeft() {
			if (this.leftIcon == "back") {
				uni.navigateBack({
					delta: 1,
					animationType: 'pop-out',
					animationDuration: 200
				});
			} else {
				this.$emit('click-left')
			}
		},
		onClickRight(url) {
			uni.redirectTo({
			    url: url
			});
		}
	}
}
